const ulStyle = {
    display: "flex",
    padding: "0",
    listStyle: "none"
}

const liStyle = {
    width: "72px",
    height: "20px"
}

const hrefStyle = {
    fontFamily: "NotoSansKR-Regular",
    fontSize: "14px",
    textAlign: "center",
    color: "#b5b5b5"
}

export default function LoginList(){
    return(
        <ul className="login_list" style={ulStyle}>
            <li className="login_item" style = {liStyle}><a href="index.html" style = {hrefStyle}>회원가입</a></li>
            <li className="login_item" style = {liStyle}><a href="index.html" style = {hrefStyle}>로그인</a></li>
        </ul>
    ); 
}